<template>
    <div class="chat-window">
        <div class="chat-code" v-if="data.type == 1 || data.type == 2">
            <p v-if="data.msg">{{data.msg}}</p>
            <input type="text" class="chat-input" v-if="data.type == 2" placeholder="">
            <div class="chat-confirm"><button @click="confirm"></button></div>
            <span class="chat-window-close" @click="close"></span>
        </div>
        <div class="chat-guess" v-else-if="data.type == 3">
            <p>{{data.msg}}</p>
            <div class="chat-guess-options">
                <a href="javascript:;" :class="[option_index == index ? 'active' : '']" @click="option_index = index" v-for="(item,index) in data.selects">{{item.text}}</a>
            </div>
            <div class="chat-confirm"><button></button></div>
            <span class="chat-window-close" id="chat-window-close"></span>
         </div>
    </div>
</template>

<script>
export default {
    props:['data'],
    data(){
        return {
            option_index: null
        }
    },
    methods:{
        close(){
            this.$emit('close')
        },
        confirm(){
            this.$emit('confirm')
        }
    }
}
</script>




